import AdminLayout from "@/layouts/Admin/index.vue";
import BotosRowPlaceHolder from "@/components/PlaceHolders/BotosRowPlaceHolder/index.vue";
import { Boto } from "@/interfaces/Boto";
import { defineComponent } from "vue";

const items: Boto[] = [];

export default defineComponent({
  name: "Botos",
  components: {
    AdminLayout,
    BotosRowPlaceHolder,
  },
  data() {
    return {
      items,
      isLoadingData: false,
      errorText: "",
      error: false,
    };
  },
  async mounted() {
    // Get Blocks
    try {
      this.isLoadingData = true;
      const botos = await this.getBotos();
      this.items = botos;
    } catch (e) {
      this.error = true;
      this.errorText = e;
    } finally {
      this.isLoadingData = false;
    }
  },
  methods: {
    async getBotos() {
      // TODO remove, just for demo purposes
      function delay(ms: number) {
        return new Promise((resolve) => setTimeout(resolve, ms));
      }
      // TODO
      await delay(300);
      const response = await fetch("https://botoapp.io/api/bots", {
        cache: "no-store",
        headers: {
          "Content-Type": "application/json",
          Authorization: "Token 4aebaaaf3a6fa58c5ddf41cbf8fe5188dfdd0278",
          origin: "*",
        },
      });
      const data = await response.json();
      return data;
    },
  },
});
